/* Estilos Sección Contacto */
.contacto {
    padding: 80px 0;
    background-color: var(--blanco);
}

.contacto h2 {
    text-align: center;
    color: var(--azul-oscuro);
    margin-bottom: 50px;
    font-size: 2.5rem;
}

.contacto-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 40px;
}

.contacto-info {
    display: grid;
    gap: 25px;
    align-content: start;
}

.contacto-item {
    background: var(--gris-claro);
    padding: 25px;
    border-radius: 8px;
    border-left: 4px solid var(--dorado);
}

.contacto-item i {
    font-size: 1.8rem;
    color: var(--dorado);
    margin-bottom: 15px;
}

.contacto-item h3 {
    color: var(--azul-oscuro);
    margin-bottom: 10px;
    font-size: 1.2rem;
}

.mapa-container {
    height: 100%;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.mapa iframe {
    width: 100%;
    height: 100%;
    min-height: 500px;

}

@media (max-width: 992px) {
    .contacto-grid {
        grid-template-columns: 1fr;
    }
    
    .mapa iframe {
        min-height: 400px;
    }
}

@media (max-width: 576px) {
    .contacto h2 {
        font-size: 2rem;
    }
    
    .contacto-item {
        padding: 20px;
    }
}

