.hero {
    background: linear-gradient(rgba(0, 35, 102, 0.7), rgba(0, 35, 102, 0.7)), url('https://images.unsplash.com/photo-1589829545856-d10d557cf95f?ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80') no-repeat center center/cover;
    height: 80vh;
    display: flex;
    align-items: center;
    color: var(--blanco);
    text-align: center;
    margin-top: 70px;
}

.hero-content {
    max-width: 800px;
    margin: 0 auto;
    padding: 0 20px;
}

.hero h2 {
    font-size: 3rem;
    margin-bottom: 20px;
}

.hero p {
    font-size: 1.2rem;
    margin-bottom: 30px;
}

/* Sección Sobre Nosotros */
.about {
    padding: 60px 0;
    background-color: var(--blanco);
    text-align: center;
}

.about h2 {
    color: var(--azul-oscuro);
    font-size: 2.3rem;
    margin-bottom: 25px;
}

.about-description {
    max-width: 800px;
    margin: 0 auto;
    font-size: 1.1rem;
    line-height: 1.8;
    color: var(--negro);
    padding: 0 20px;
}

.btn {
    display: inline-block;
    padding: 12px 25px;
    background-color: var(--dorado);
    color: var(--azul-oscuro);
    border-radius: 4px;
    font-weight: 600;
    transition: all 0.3s ease;
    border: none;
    cursor: pointer;
}

.btn:hover {
    background-color: var(--azul-oscuro);
    color: var(--blanco);
    transform: translateY(-2px);
}


@media (max-width: 768px) {
    .about h2 {
        font-size: 1.8rem;
    }
    
    .about-description {
        font-size: 1rem;
    }
}