.container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
}

/* Sección Sobre Nosotros */
.about {
    padding: 60px 0;
    background-color: var(--blanco);
    text-align: center;
}

.about h2 {
    color: var(--azul-oscuro);
    font-size: 2.3rem;
    margin-bottom: 25px;
}

.about-description {
    max-width: 800px;
    margin: 0 auto;
    font-size: 1.1rem;
    line-height: 1.8;
    color: var(--negro);
    padding: 0 20px;
}

@media (max-width: 768px) {
    .about h2 {
        font-size: 1.8rem;
    }
    
    .about-description {
        font-size: 1rem;
    }
}
