/* Servicios */
.servicios {
    padding: 80px 0;
    background-color: var(--gris-claro);
}

.servicios h2 {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 50px;
    color: var(--azul-oscuro);
}

.servicios-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
}

.servicio-card {
    background-color: var(--blanco);
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    padding: 30px;
    text-align: center;
    cursor: pointer;
}

.servicio-card:hover {
    transform: translateY(-10px);
}

.servicio-icon {
    font-size: 2.5rem;
    color: var(--dorado);
    margin-bottom: 20px;
}

.servicio-card h3 {
    font-size: 1.5rem;
    margin-bottom: 15px;
    color: var(--azul-oscuro);
}

/* Estilos para los modales */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000;
    display: none;
}

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--blanco);
    padding: 30px;
    border-radius: 8px;
    z-index: 1001;
    max-width: 600px;
    width: 90%;
    display: none;
}

.modal-content {
    position: relative;
    text-align: justify;
}

.close-modal {
    position: absolute;
    top: -15px;
    right: -15px;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--azul-oscuro);
}

.modal-body {
    padding: 20px 0;
}

.modal-body li{
    margin-left: 15px;
}

.modal.active,
.modal-overlay.active {
    display: block;
}

@media (max-width: 768px) {
    .modal {
        padding: 20px;
    }
}